import { levenshtein } from '../../matcher/levenshtein';

describe('Test levenshtein', () => {
  it('test distance', () => {
    expect(levenshtein('abcabc', 'abc')).toBe(3);

    expect(levenshtein('abcdef', 'bcd')).toBe(3);

    expect(levenshtein('abcdef', 'abedef')).toBe(1);

    expect(levenshtein('abcdef', 'bedef')).toBe(2);

    expect(levenshtein('abcdef', 'abeef')).toBe(2);
  });
});
